import axios from "axios";
import React from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import { PostDiv, Post, BtnDiv } from "../Style/DetailCSS";
import { useSelector } from "react-redux";

import moment from "moment";
// moment   
import "moment/locale/ko";

function Detail(props) {
  let navigate = useNavigate();
  let params = useParams();
  const user = useSelector((state) => state.user);

  const DeleteHandler = () => {
    if (window.confirm("  ?")) {
      let body = {
        postNum: params.postNum,
      };

      axios
        .post("/api/post/delete", body)
        .then((res) => {
          if (res.data.success) {
            alert(" .");
            navigate("/");
          }
        })
        .catch(() => {
          alert("  .");
        });
    }
  };

  const SetTime = (a, b) => {
    if (a !== b) {
      return moment(b).format("YYYY MMMM Do  hh:mm") + "()";
    } else {
      return moment(a).format("YYYY MMMM Do  hh:mm");
    }
  };

  return (
    <PostDiv>
      <Post>
        <h1>{props.PostInfo.title}</h1>
        <p className="author">{props.PostInfo.author.displayName}</p>
        {props.PostInfo.image ? (
          <img
            src={`http://localhost:8080/${props.PostInfo.image}`}
            alt=""
            style={{ width: "100%", height: "auto" }}
          />
        ) : null}
        <p>{props.PostInfo.content}</p>
        <p className="time">
          {SetTime(props.PostInfo.createdAt, props.PostInfo.updatedAt)}
        </p>
      </Post>
      {user.uid === props.PostInfo.author.uid && (
        <BtnDiv>
          <Link to={`/edit/${props.PostInfo.postNum}`}>
            <button className="edit"></button>
          </Link>
          <button className="delete" onClick={() => DeleteHandler()}>
            
          </button>
        </BtnDiv>
      )}
    </PostDiv>
  );
}

export default Detail;
